export default {
    // 设置cookie exdays设置cookie的有效期，单位为天
    set(cname: string, cvalue: string | boolean, exdays: number) {
        const d = new Date();
        let expires;
        d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
        expires = 'expires=' + d.toUTCString();
        document.cookie = cname + '=' + cvalue + ';' + expires;
    },
    get(cname: string) {
        const name = cname + '=';
        const ca = document.cookie.split(';');
        for (const i of ca) {
            const c = i.trim();
            if (c.indexOf(name) === 0) {
                return c.substring(name.length, c.length);
            }
        }
        return false;
    },
};
